
	import ViewBase from '../Core/Base';
	import { Constants, ModelVenue, ModelUser } from '@chalkysticks/sdk';
	import { Component, Prop } from 'vue-property-decorator';

	/**
	 * @class VenueCard
	 * @package Venue
	 * @project ChalkySticks SDK Vue2.0 Components
	 */
	@Component
	export default class VenueCard extends ViewBase {
		/**
		 * @type ChalkySticks/Model/Venue
		 */
		@Prop({
			default: () => new ModelVenue,
		})
		public venueModel!: ModelVenue;

		/**
		 * @return void
		 */
		public attachEvents(): void {

		}

		/**
		 * @return void
		 */
		public detachEvents(): void {

		}

		/**
		 * @return string
		 */
		protected getMapUrl(): string {
			let output: string;

			// Set root url
			output = 'https://www.google.com/maps/place/';

			// Set address
			output += encodeURIComponent(this.venueModel.getAddress());

			return output;
		}

		// region: Event Handlers
		// ---------------------------------------------------------------------------

		// endregion: Event Handlers
	}
