
	import ViewBase from '../Core/Base';
	import { Constants, CollectionVenue, ModelVenue, ModelUser } from '@chalkysticks/sdk';
	import { Component, Prop } from 'vue-property-decorator';

	/**
	 * We can use the following modifiers as classes:
	 *
	 * size-sm
	 *
	 * @class VenueList
	 * @package Venue
	 * @project ChalkySticks SDK Vue2.0 Components
	 */
	@Component
	export default class VenueList extends ViewBase {
		/**
		 * @type ChalkySticks/Collection/Venue
		 */
		@Prop({
			default: () => new CollectionVenue({
				baseUrl: Constants.API_URL_V1,
				qp: {
					limit: 5,
				},
			}),
		})
		public venueCollection!: CollectionVenue;

		/**
		 * @constructor
		 */
		constructor() {
			super();

			// Check if we need to load data
			if (!this.venueCollection.length) {
				this.venueCollection.fetch();
			}
		}

		/**
		 * @return void
		 */
		public attachEvents(): void {

		}

		/**
		 * @return void
		 */
		public detachEvents(): void {

		}

		// region: Event Handlers
		// ---------------------------------------------------------------------------

		/**
		 * @param MouseEvent e
		 * @return Promise<void>
		 */
		protected async Handle_OnClickNext(e: MouseEvent): Promise<void> {
			e.preventDefault();

			this.venueCollection.fetchNext();
		}

		/**
		 * @param MouseEvent e
		 * @return Promise<void>
		 */
		protected async Handle_OnClickPrevious(e: MouseEvent): Promise<void> {
			e.preventDefault();

			this.venueCollection.fetchPrevious();
		}

		// endregion: Event Handlers
	}
